/*-----header-----*/
@media screen and (max-width: 480px)  {
  .header{
    width: 100% !important;
    min-width: auto!important;
  }


  .headerRightSection{
    display: none!important;
  }

  .headerLeftSection{
    width: 100%!important;
  }

  .headerLogoContainer{
    text-align: center;
  }

  .navContainer{
    padding: 17px 4px 10px 0px !important;
    width: 66%!important;
  }

  .list li{
    margin-left: 25px !important;
  }

  .list li:active,
  list li:focus{
    outline: none;
  }
  .mainContainer{
    width: 100%;
    flex-direction: column;
    min-width: auto!important;
  }

  .homeMain{
    padding-left: 25px;
  }

  h2{
    font-size: 1.2rem!important;
  }

  .mainLeft{
    width: 100%;
  }

  .mainRight{
    width: 100%;

  }

  .rightNavList li {
    font-size: 16px;
  }

  .footerBody {
    width: 94%!important;
    min-width: auto!important;
  }
  .textAlign{
    text-align: center;
  }
  .auto{
    width: 100%!important;
  }
  .img100{
    width: 100%!important;
  }

  .footerLeft{
    width: 50%!important;
  }

  .displayBtn{
    display: block!important;
    padding-left: 5px;

  }
  .active,
  active:focus,
  active:active{
    background-color: #538234 !important;
  }
  button,
  button:active,
  button:focus {
    outline: none!important;
  }

  .Categories{
    display: none!important;
  }

  .rightNavList{
    display: none!important;
  }

  .socialMediaBody{
    margin-top: 35px;
  }

  .shadows{
    box-shadow: 0 0 10px rgba(0,0,0,0.7)!important;
    margin-top: 5px!important;
  }

  .btnPosition{
    margin-top: 11px;
  }
}

@media screen and (max-width: 380px)  {
  .displayBtn{
    display: block!important;

  }
  .list li{
    margin-left: 20px !important;
  }
}