
.rightNavigation{
  width: 100%;
  display: flex;
  flex-direction: column;
}

.Categories{
  width: 100%;
  height: 55px;
  background-color: #538234;
  color: white;
  padding: 10px 12px;
  margin-top: 50px;
}

.Categories p{
  font-weight: 600;
  font-size: 22px;
}

.rightNavList{
  width: 100%;
  padding: 10px;
  background-color: #FFFFFF;
}

.rightNavList li{
  border-bottom: 1px solid #dddddd ;
  color: #538234;
  list-style-type: none;
  font-size: 18px;
  font-weight: 600;
  padding: 5px 4px;
  transition: all .3s;
  cursor: pointer;
}

.rightNavList li:hover{
  color: #888888;
}


